.news,
.hits,
.partners {
  background-color: #fff;
  padding-top: 20px;
  padding-bottom: 20px;
  .slick-prev,
  .slick-next {
    position: absolute;
    top: 30%;
    background-color: transparent;
    z-index: 10;
    font-size: 0; }
  .slick-prev {
    left: -20px;
    @include arr(20, 23, #828282, l);
    &:hover {
      border-color: transparent #fc2db7 transparent; }
    &:active {
      border-color: transparent #9f9f9f transparent; } }
  .slick-next {
    right: -20px;
    @include arr(20, 23, #828282, r);
    &:hover {
      border-color: transparent transparent transparent #fc2db7; }
    &:active {
      border-color: transparent transparent transparent #9f9f9f; } }
  h1 {
    margin-left: 30px; } }
.slider-six {
  margin-top: 25px;
  margin-left: 30px;
  margin-right: 30px;
  &__in {
    width: 180px !important;
    position: relative;
    height: 250px;
    & + div {
      margin-left: 20px; } }
  &__img {
    max-width: 100%;
    height: 135px;
    margin-bottom: 5px; }
  &__link {
    color: #000000;
    font-size: 18px;
    font-weight: 400;
    &:active {
      color: #9f9f9f; } }
  &__price {
    color: #444444;
    font-size: 22px;
    font-weight: 400;
    position: absolute;
    right: 18px;
    bottom: 0; } }

.slider-four {
  margin-top: 25px;
  margin-left: 30px;
  margin-right: 30px;
  &__in {
    width: 240px !important;
    position: relative;
    height: 150px;
    & + div {
      margin-left: 60px; } }
  &__img {
    max-width: 100%;
    margin-bottom: 5px; }
  &__link {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    color: #000000;
    font-size: 18px;
    font-weight: 400;
    &:active {
      color: #9f9f9f; } } }



