@include font('pfdin-regular', 'pfdintextcondpro-regular');
@include font('pfdin-italic', 'pfdintextcondpro-italic');
@include font('tt0248', 'tt0248m');
@include font('tt0249', 'tt0249m');

body {
    background-color: #eee;
    min-width: 1254px;
    font-family: 'pfdin-regular', sans-serif; }

.container {
    width: 1254px;
    margin: auto; }

.clr {
  @include clr; }

a {
  text-decoration: none;
  &:hover {
    text-decoration: underline; }
  &:active {
    text-decoration: underline; } }

.text-center {
  text-align: center; }

.section {
  & + .section {
    margin-top: 40px; }
  &__title {
    font-family: "PF Din Text Cond Pro";
    font-size: 30px;
    font-weight: 400;
    line-height: 40px;
    text-transform: uppercase;
    a {
      color: #444444;
      &:hover {
        color: #fc2db7;
        text-decoration: none; }
      &:active {
        color: #9f9f9f; } } }

  &__hr {
    border-top: 0;
    border-bottom: 1px solid #eee;
    &--pink {
      border-color: #fb2cb6; } } }

.btn {
  &--more {
    margin-top: 20px;
    padding: 10px 15px;
    color: #eeeeee;
    font-size: 22px;
    font-weight: 400;
    background-color: #828282;
    &:hover {
      background-color: #fc2db7; }
    &:active {
      background-color: #9f9f9f; } } }


