.pagination {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;
  &__item {
    display: inline-block;
    & + & {
      margin-left: 10px; }
    a {
      color: #000000;
      font-size: 22px;
      font-weight: 400;
      &:hover,
      &.active {
        text-decoration: none;
        color: #fc2db7; }
      &.active {
        &:hover {
          color: #010001; } } } } }
